:root {
  --palid: #f0ebe5;
  --salmon: #ff975a;
  --darkpine: #0c474b;
  --lightgreen: #b6c8c9;
  --white: #ffffff; }

body {
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: normal; }

.semibold {
  font-weight: 600; }

.regular {
  font-weight: 400; }

.font-size-24 {
  font-size: 24px; }

.font-size-20 {
  font-size: 20px; }

.font-size-14 {
  font-size: 14px; }

.animation {
  transition: all 1s; }

.animation:hover {
  background-color: var(--white);
  color: var(--darkpine); }

/*HEADER:LOGO + MENU BURGUER */
header {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  width: 100%;
  margin-top: 19px; }
  header nav {
    display: none; }

.menu-burguer {
  position: absolute;
  left: 36px; }

/* PRIMERA SECCIÓN:IMAGEN DE FONDO + texto+ BOTÓN*/
.h1 {
  color: var(--darkpine); }

.container-1 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: end;
  background-image: url(../images/header.jpg);
  background-size: cover;
  height: 100vh;
  background-position: 50% 50%; }

.button {
  border: none;
  background-color: var(--darkpine);
  color: var(--white);
  padding: 12px 24px;
  margin-bottom: 64px;
  margin-top: 24px;
  font-size: 16px; }

/*SEGUNDA SECCIÓN: VOLVERSE ORGANIZADO*/
/*.container-2 {
  color: var(--darkpine);
