.container-2 {
  color: var(--darkpine);
  background-color: var(--palid);
  padding: 41px 45px 18px;
  text-align: center;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto; }
  .container-2 img {
    margin: 47px 0px 37px; }
  .container-2 h4 {
    margin: 12px 0px 24px; }
  .container-2 div,
  .container-2 h2 {
    margin: 15px 0px; }

/* TERCERA SECCIÓN:ENVÍO + BOTÓN*/
.container-3 {
  color: var(--white);
  background-color: var(--salmon);
  text-align: center; }
  .container-3 img {
    margin: 76px 0px 18px; }
  .container-3 h3 {
    margin: 18px 0px 20px; }
  .container-3 button {
    text-decoration: underline;
    background-color: transparent;
    margin: 20px 0px 56px; }

/*CUARTA SECCIÓN: DISFRUTA + BOTÓN*/
.container-4 {
  color: var(--darkpine);
  text-align: center;
  background-color: var(--palid);
  padding: 87px 27px; }
  .container-4 h3 {
    margin-bottom: 10px; }
  .container-4 button {
    margin-bottom: 0px; }

/*CONTAINER 5 FOTO*/
.container-5 {
  background-image: url(../images/creando-espacios.jpg);
  background-size: cover;
  height: 304px;
  background-position: 50% 50%; }

/*FOOTER*/
.footer {
  background-color: var(--darkpine);
  color: var(--lightgreen);
  display: flex;
  flex-direction: column;
  padding: 56px 100px 63px; }
  .footer h5 {
    margin: 20px 0; }
  .footer ul {
    margin-bottom: 32px; }
  .footer li {
    margin-bottom: 5px; }
  .footer .social {
    display: flex;
    flex-direction: row; }
  .footer i {
    margin-right: 11px; }
  .footer a {
    text-decoration: none;
    color: var(--ligthgreen); }

.scroll-up {
  position: fixed;
  right: 29px;
  bottom: 16px;
  color: var(--white);
  padding: 10px;
  border-radius: 5px; }

@media (min-width: 768px) {
  .h1 {
    font-size: 36px; }
  .container-1 {
    align-items: start;
    justify-content: center;
    padding: 0px 147px; }
  .container-2 {
    grid-template-columns: 1fr 1fr 1fr; }
    .container-2 .h2 {
      grid-column: 1/4; }
  .container-5 {
    height: 474px; }
  .footer {
    flex-direction: row;
    justify-content: space-between; }
  .scroll-up {
    bottom: 87px; } }

@media (min-width: 1200px) {
  header {
    display: grid;
    grid-template-columns: 4fr 1fr 4fr;
    text-align: center; }
    header nav {
      display: block; }
    header a {
      text-decoration: none;
      color: var(--darkpine); }
    header li {
      display: inline;
      margin: 0 10px; }
    header .menu-burguer {
      display: none; }
  .container-2 h2 {
    font-size: 36px; }
  .container-2 h3 {
    font-size: 24px; }
  .container-2 img {
    margin-bottom: 10px; }
  .container-3 h3 {
    font-size: 36px;
    margin-top: 0; }
  .container-3 img {
    margin-bottom: 0; }
  .container-4 h3 {
    font-size: 36px; }
  .parent-4-5 {
    background-color: var(--palid);
    display: flex;
    justify-content: space-around;
    align-items: center; }
    .parent-4-5 section {
      width: 100%; } }
